@import '../../../style/variables';

.button{
  padding: .4rem 2.4rem;
  background-color: $yellow;
  color: #FFF;
  font-size: 1.4rem;
  font-weight: 700;
  line-height: 2.8rem;
  border-radius: .8rem;
  text-align: center;
  transition: color .4s ease;
  &:hover {
    color: #473E43;
  }
}